const app = getApp();
// 获取公共域名地址
import serverUrl from '@/common/serveUrl.js';
export default {
	data () {
		return {
			imgUrl:this.$imgUrl,
			currentId:'',/* 控制被选中 */
			currentIndex:'',/* 控制位置 */
			brands:[],
			isShow: false,
			//搜索
			category: '',
			list: [
				'茶叶',
				'速食',
				'饮品',
				'饮品'
			],
			//轮播图
			photo: [],
			pageRequest: 1,
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			//推荐商品列表
			shopList: []
		}
	},
	onLoad() {
		this.handle();
		this.recom();
		this.phrtr()
	},
	// 模态框显示隐藏
	showModal(e) {
	    this.setData({
	      modalName: e.currentTarget.dataset.target
	    })
	},
	hideModal(e) {
	    this.setData({
	      modalName: null
	    })
	},
	onTabItemTap() {
	    if (app.globalData.userInfo){
	       return
	    }
	    uni.showModal({
	        title: '提示',
	        content: '您尚未注册，是否马上去注册页面？',
	        success: function (sm) {
				if (sm.confirm) {
				    uni.redirectTo({
				       url: '/pages/application/member',
				    })
				} else{
				    uni.switchTab({
				       url: '/pages/index/index'
				    })
				}
			},
		})
	},
	methods: {
		//轮播图
		phrtr() {
			const url = serverUrl.serverUrl;
			uni.request({
				url: 'https://wx.zzsnqyjxh.org/test/app/appImage/findByImg',
				data: {
					pageRequest: this.pageRequest
				},
				method: 'GET',
				success: res => {
					console.log(res.data.data.data)
					if (res.data.code == 200) {
						this.photo = res.data.data.data
					}
				}
			})
		},
		// 点击并切换到推荐商品
		clickShop(e){
			var that = this;
			var recommedId = e.target.dataset.index
			// console.log(recommedId)
			var url=" https://wx.zzsnqyjxh.org/test";
			uni.request({
				url:"https://wx.zzsnqyjxh.org/test/app/Goods/findByCategoryId?goodsCategoryId="+recommedId,
				method:"POST",
				success(res) {
					// console.log(res.data.data.data)
					var recommedList = res.data.data.data
					that.shopList=recommedList
				}
			})
	
		},
		//滑动导航栏
		handleScroll(index) {
			this.currentId = index;
			this.currentIndex = Math.max(0, index - 1);
		},
		//获取导航栏数据
		handle() {
			var url=" https://wx.zzsnqyjxh.org/test";
			uni.request({
				url: 'https://wx.zzsnqyjxh.org/test/app/appCategory/findCategory',
				data: '',
				methods: 'GET',
				success: res => {
					if(res.data.code == 200) {
						this.brands = res.data.data.data
					}
				}
			})
		},
		//推荐商品
		recom() {
			var that = this;
			var url=" https://wx.zzsnqyjxh.org/test";
			uni.request({
				url: 'https://wx.zzsnqyjxh.org/test/app/Goods/findRecommend',
				method: 'get',
				success: res => {
					console.log(res)
					if (res.data.code == 200) {
						that.shopList = res.data.data.data
					}
				}
			})
		},
		//搜索
		searchMe() {
			const url = serverUrl.serverUrl;
			var keyword = this.category
			// if(keyword) {
			//     location.href = "#/shop?keyword="+keyword;
			// }
			// var keyword = getParamsByUrl (location.href,'keyword')
			// function getParamsByUrl (url,name) {
			//      var params=url.substr(url.indexOf('?')+1);
			//      var param=params.split();
			//      for(var i=0;i<param.length;i++){
			//         var current=param[i].split('=');
			//         if(current[0]==name){
			//             return current[1]
			//         }
			//     }
			//       return null;
			// }
			uni.request({
				url: 'https://wx.zzsnqyjxh.org/test/app/Goods/findGoodsByCategory?category='+this.category,
				// data: {
				// 	category: this.category
				// },
				method: 'POST',
				success: (res) => {
					console.log(res);
					this.shopList = res.data.data
				}
			})
		},
		// 头部轮播
		changeIndicatorDots(e) {
		    this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
		    this.autoplay = !this.autoplay
		},
		intervalChange(e) {
		    this.interval = e.target.value
		},
		durationChange(e) {
		    this.duration = e.target.value
		},
		//跳转到详情页
		turnMovie(e) {
			console.log(e)
			var itemId = e.currentTarget.dataset.indes
			uni.navigateTo({
				url: "/pages/shop/shop-details?id=" + itemId
			});
		},
	
	}
}
